.mobinav {
  border-left: 1px solid #d1d4d9;
  border-right: 1px solid #d1d4d9;
  border-bottom: 1px solid #d1d4d9;
  max-width: 300px;
  background-color: white;
  -webkit-transform: translateX(-100%) scale(1, 0.1);
          transform: translateX(-100%) scale(1, 0.1);
  -webkit-transform-origin: top left;
          transform-origin: top left;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.mobinav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.mobinav ul li {
  border-top: 1px solid #efefef;
  opacity: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
}
.mobinav ul li a {
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  line-height: 1rem;
  text-decoration: none;
  color: #8a8d92;
  display: block;
  padding: 1rem;
}
.mobinav ul li:first-child {
  border-top: none;
}
.mobinav ul li:hover a {
  color: white;
  background-color: #6d747e;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.mobinav ul li:hover a i {
  color: white;
}

..mobinavBtn:checked ~ .mobinav {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

..mobinavBtn:checked ~ .mobinav ul li {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
}

..mobinavBtn {
  display: none;
